<template>
    <div>
        <ul>
            <li class="list-group-item"
            :class="{ active: index == currentIndex }"
            v-for="(backup, index) in backup_states"
            :key="index">
                {{ backup.name }}
                <div class="float-right spinner-grow spinner-grow-sm m-1" role="status" v-if="backup.last_status=='[RUNNING]'" :key="index"></div>

                <div class="float-right sm mr-1" role="status" v-if="backup.last_status=='[ALL OK]'" :key="index">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="green" class="bi bi-check-circle-fill" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                    </svg>
                </div>

                <div class="float-right sm mr-1" role="status" v-if="false" :key="index">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-exclamation-circle-fill" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0M8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4m.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2"/>
                    </svg>
                </div>

                <div class="float-right sm mr-1" role="status" v-if="backup.last_status=='[NOT OK]'" :key="index">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="red" class="bi bi-x-circle-fill" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0M5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293z"/>
                    </svg>
                </div>

                <div class="float-right sm mr-1" role="status" v-if="backup.last_status=='[FINISHED WITH ERRORS]'" :key="index">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="orange" class="bi bi-exclamation-circle-fill" viewBox="0 0 16 16">
                        <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0M8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4m.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2"/>
                      </svg>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
import BackupTracesDataService from "../services/BackupTracesDataService";
export default {
    name: "general-status",
      data() {
        return {
            backup_states: []
        };
      },
      methods: {
        getStatus(){
            BackupTracesDataService.generalStatus()
            .then(response => {
                this.backup_states = response.data.reverse();
            })
            .catch(e => {
                console.log(e);
            });
        }
      },
      mounted() {
        this.getStatus();
        this.timer = setInterval(() => {
            this.getStatus();
        }, 300)
      }
}
</script>
